.box {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ccc;
}

.small {
  composes: box;
  width: 50px;
  height: 50px;
}

.medium {
  composes: box;
  width: 100px;
  height: 100px;
}

.large {
  composes: box;
  width: 150px;
  height: 150px;
}
